.skill-tag {
  display: inline-flex;
  min-width: 275;
  padding: 10px 20px;
  align-items: center;
  background-color: var(--panel-bg);
  border-radius: 12px;
}

.skill-tag>label {
  color: var(--primary-font-color);
  font-weight: bold;
  pointer-events: none;
}

.skill-tag .skill-icon {
  display: inline-block;
  width: 45px;
  height: 45px;
  margin-right: 16px;
  transform: rotate(271deg);
}

.skill-tag .skill-icon-inner>img {
  max-width: 100%;
  max-height: 100%;
}

.skill-tag .skill-icon .skill-icon-bar {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  height: 45px;
  width: 45px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  pointer-events: none;
  z-index: 2;
}

.skill-tag .skill-icon .skill-icon-inner {
  position: relative;
  top: 1px;
  left: 1px;
  height: 42px;
  width: 42px;
  background-color: #e8e8e8;
  border: 1px solid #7D7D7D;
  border-radius: 50%;
  transform: rotate(-271deg);
  overflow: hidden;
  z-index: 1;
}