.profile-bar,
.profile-links>ul>li {
  display: flex;
  align-items: center;
}

.profile-avatar {
  display: inline-flex;
  height: 80px;
  width: 80px;
  min-width: 80px;
  margin-right: 56px;
  align-items: center;
  background-color: #190337;
  border-radius: 50%;
  justify-content: center;
  overflow: hidden;
}

.profile-avatar>img {
  max-width: 100%;
  max-height: 100%;
}

.profile-links>ul {
  padding-left: 0;
  list-style-type: none;
  white-space: nowrap;
}

.profile-links>ul>li {
  display: inline-block;
  font-size: 26px;
}

.profile-links>ul>li>a {
  color: var(--primary-font-color);
}

.profile-links>ul>li:not(:last-of-type) {
  margin-right: 56px;
}

@media (max-width: 1350px) {
  .profile-avatar {
    height: 72px;
    width: 72px;
    min-width: 72px;
    margin-right: 32px;
  }

  .profile-links>ul>li {
    font-size: 18px;
  }

  .profile-links>ul>li:not(:last-of-type) {
    margin-right: 36px;
  }

  .profile-links>ul>li:first-child .image-icon:first-of-type {
    width: 24px !important;
    height: 18px !important;
  }

  .profile-links>ul>li:nth-child(2) .image-icon:first-of-type {
    width: 18px !important;
    height: 18px !important;
  }

  .profile-links>ul>li:first-child .image-icon:last-of-type,
  .profile-links>ul>li:nth-child(2) .image-icon:last-of-type {
    width: 17px !important;
    height: 17px !important;
  }
}

@media (max-width: 1024px) {
  .profile-avatar {
    height: 56px;
    width: 56px;
    min-width: 56px;
    margin-right: 25px;
  }

  .profile-links>ul>li:not(:last-of-type) {
    margin-right: 25px;
  }
}