.info-card {
  padding: 25px;
  background-color: var(--panel-bg);
  border-radius: 15px;
}

.info-card:not(:last-of-type) {
  margin-bottom: 10px;
}

.info-tags,
.info-card>h4,
.info-card>h5 {
  margin-top: 0;
  color: var(--primary-font-color);
}

.info-card>h4 {
  margin-bottom: 8px;
  font-size: 24px;
}

.info-card>h5 {
  margin-bottom: 0;
  font-size: 16px;
}

.info-card>p {
  margin-top: 15px;
  margin-bottom: 0px;
  color: var(--secondary-font-color);
  font-size: 14px;
}

.info-tags {
  display: flex;
  margin-top: 25px;
  margin-bottom: 0px;
  padding-left: 0;
  align-items: center;
  font-size: 14px;
  list-style-type: none;
}

.info-tags>li {
  display: inline-flex;
  align-items: center;
}

.info-tags>li:not(:last-child) {
  margin-right: 16px;
}

.info-tags>li>span {
  display: inline-flex;
  line-height: normal;
}

.info-tags>li .tag-icon {
  width: 14px;
  height: 14px;
  margin-right: 6px;
  align-items: center;
  border-radius: 3px;
  justify-content: center;
  overflow: hidden;
}

.info-tags>li .tag-icon>img {
  height: 100%;
  width: 100%;
}

@media (max-width: 1024px) {
  .info-card>h4 {
    font-size: 22px;
  }

  .info-tags {
    font-size: 12px;
    list-style-type: none;
  }

  .info-tags>li .tag-icon {
    width: 12px;
    height: 12px;
  }
}