.nav-sections {
  display: flex;
  flex: 1;
  padding-left: .2rem;
  justify-content: center;
  flex-direction: column;
  list-style-type: none;
}

.nav-item:not(:last-of-type) {
  margin-bottom: 45px;
}

.nav-item>a {
  display: flex;
  align-items: center;
  color: var(--secondary-font-color);
  font-weight: 500;
  font-size: 16px;
  text-decoration: none;
  text-transform: uppercase;
}

.nav-item-line {
  display: flex;
  margin-right: 36px;
}

.nav-item-line>span:first-child {
  width: 55px;
}

.nav-item-line>span:not(:first-child) {
  width: 5px;
}

.nav-item-line>span {
  display: inline-block;
  margin-right: 2px;
  height: 2px;
  background-color: var(--secondary-font-color);
  border-radius: 3px;
  transition: all .3s ease;
}

.nav-item-line>span:nth-child(2),
.nav-item-line>span:nth-child(3) {
  width: 0;
  margin-right: 0;
}

.nav-item.is-current>a,
.nav-item.is-current .nav-item-line>span {
  color: var(--primary-font-color);
}

.nav-item.is-current .nav-item-line>span {
  background-color: var(--primary-font-color);
}

.nav-item.is-current .nav-item-line>span:nth-child(2),
.nav-item.is-current .nav-item-line>span:nth-child(3) {
  width: 5px;
  margin-right: 2px;
}

@media (max-width: 988px) {
  .nav-sections {
    padding: 0;
    flex: 0;
  }

  .nav-item {
    display: none;
  }

  .show-float-menu .nav-item.is-current {
    position: fixed;
    display: block;
    top: 0;
    left: 0;
    right: 0;
    padding: 1rem;
    background-color: #2b2b2b;
    z-index: 10;
  }
}