:root {
  --primary-font-color: #F5F5F5;
  --secondary-font-color: #C5C5C5;
  --panel-bg: rgba(36, 2, 70, .7);
}

*,
body,
html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body,
html {
  font-family: 'Roboto', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}

h1,
h2,
h3,
h4,
h5 {
  font-family: 'Lato', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}

body {
  margin: 0;
  background: rgb(161, 0, 209);
  background: -moz-linear-gradient(320deg, rgba(161, 0, 209, 1) 0%, rgba(105, 0, 185, 1) 30%, rgba(72, 0, 145, 1) 60%, rgba(12, 0, 36, 1) 100%);
  background: -webkit-linear-gradient(320deg, rgba(161, 0, 209, 1) 0%, rgba(105, 0, 185, 1) 30%, rgba(72, 0, 145, 1) 60%, rgba(12, 0, 36, 1) 100%);
  background: linear-gradient(320deg, rgba(161, 0, 209, 1) 0%, rgba(105, 0, 185, 1) 30%, rgba(72, 0, 145, 1) 60%, rgba(12, 0, 36, 1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#a100d1", endColorstr="#0c0024", GradientType=1);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  color: var(--secondary-font-color);
  text-decoration: none;
}

.image-icon {
  display: inline-block;
  min-height: 10px;
  min-width: 10px;
  margin-bottom: -3px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}