/* autoprefixer grid: autoplace */

.main-wrapper {
  width: 100vw;
  height: 100vh;
  background: url("./assets/images/bg-image.png") no-repeat center;
  background-size: cover;
  color: #f5f5f5;
  overflow: auto;
}

.main-content {
  position: relative;
  display: flex;
  height: auto;
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
}

.fixed-content-wrapper {
  flex-basis: 45%;
}

.fixed-content {
  position: sticky;
  top: 0;
  left: 0;
  display: flex;
  height: 100vh;
  padding: 2.5rem 1.25rem 2.5rem 2.5rem;
  flex-direction: column;
  overflow: hidden;
}

.fixed-content>h1 {
  margin-bottom: 50px;
  color: var(--primary-font-color);
  line-height: 1;
  font-size: 68px;
}

.fixed-content>p {
  margin-top: 0;
  margin-bottom: 45px;
  color: var(--secondary-font-color);
  font-size: 20px;
}

.btn-get-touch {
  display: inline-block;
  width: 180px;
  padding: 13.5px 26px;
  background-color: #3e3744;
  border-radius: 25px;
  color: var(--primary-font-color);
  font-size: 14px;
  font-weight: 500;
  text-decoration: none;
  text-align: center;
  transition: all .3s ease;
}

.btn-get-touch:hover {
  background-color: #372c40;
}

.scrollable-content {
  padding: 2.5rem 2.5rem 2.5rem 1.25rem;
  flex-basis: 55%;
}

.section-content {
  margin-bottom: 100px;
}

.section-content:first-of-type {
  margin-top: 40px;
}

.section-content>h3 {
  margin-top: 0;
  color: var(--secondary-font-color);
  font-size: 25px;
  text-transform: uppercase;
}

.skillset-header {
  display: flex;
  margin-bottom: 45px;
  align-items: center;
  justify-content: space-between;
}

.skillset-label {
  display: flex;
  align-items: center;
}

.skillset-label>label {
  color: var(--primary-font-color);
  font-size: 24px;
  font-weight: bold;
}

.skillset-legend {
  display: inline-block;
  padding: 6px 15px;
  background-color: #4C4B4D;
  border-radius: 15px;
}

.skillset-legend .skillset-legend-dot {
  display: inline-block;
  position: relative;
  top: 2px;
  margin-right: 10px;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  pointer-events: none;
}

.skillset-legend .skillset-legend-dot.beginner {
  background-color: #8D8C8E;
}

.skillset-legend .skillset-legend-dot.intermediate {
  background-color: #24AFFF;
}

.skillset-legend .skillset-legend-dot.advanced {
  background-color: #BD36FF;
}

.skillset-legend>ul {
  display: flex;
  margin: 0;
  padding-left: 0;
  align-items: center;
  list-style-type: none;
}

.skillset-legend>ul>li {
  display: inline-block;
  font-size: 16px;
}

.skillset-legend>ul>li:not(:last-of-type) {
  margin-right: 38px;
}

.skillset-list>label {
  display: block;
  margin-bottom: 25px;
  color: var(--secondary-font-color);
  font-weight: 500;
  font-size: 24px;
}

.skillset-section:not(:last-of-type) .skillset-list {
  margin-bottom: 50px;
}

.skillset-list-content {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 10px;
}

.handcrafted-msg {
  margin-top: 0;
  color: var(--secondary-font-color);
  text-align: center;
}


@media (max-width: 1350px) {
  .fixed-content>h1 {
    font-size: 62px;
  }

  .fixed-content>p {
    font-size: 16px;
  }

  .section-content>h3 {
    font-size: 22px;
  }

  .skillset-label>label,
  .skillset-list>label {
    font-size: 20px;
  }

  .skillset-legend>ul>li {
    font-size: 14px;
  }

  .skillset-legend-dot {
    width: 12px !important;
    height: 12px !important;
  }

  .skillset-legend>ul>li:not(:last-of-type) {
    margin-right: 10px;
  }
}

@media (max-width: 1024px) {
  .fixed-content>h1 {
    font-size: 48px;
  }

  .fixed-content>p {
    font-size: 15px;
  }

  .section-content>h3 {
    font-size: 18px;
  }

  .skillset-legend>ul>li {
    font-size: 12px;
  }
}

@media (max-width: 988px) {
  .main-content {
    display: block;
  }

  .fixed-content {
    height: auto;
  }

  .fixed-content-wrapper,
  .scrollable-content {
    width: 100%;
    flex-basis: 100%;
  }
}